@media (orientation: portrait) {
    :root {
        --buttonSsize: 40px
    }
}

@media (orientation: landscape) {
    :root {
        --buttonSsize:60px
    }
}

.rhap_container {
    background-color: #ffffff00;
    box-shadow: none;
    padding: 0;
}

.rhap_play-pause-button {
    color: #a8b6af;

    width: var(--buttonSsize);
    height: var(--buttonSsize);
}

.rhap_play-pause-button > svg {
    width: var(--buttonSsize);
    height: var(--buttonSsize);
}