@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+KR:wght@300;500;700&display=swap');
/*@import url('https://fonts.googleapis.com/css2?family=Alex+Brush&family=EB+Garamond&family=Lato&display=swap');*/
@import url('https://fonts.googleapis.com/css2?family=Alex+Brush&family=Lato&family=Merriweather&display=swap');
@import url('https://hangeul.pstatic.net/hangeul_static/css/maru-buri.css');
@import url("./static/fonts/font.css");

body {
  margin: 0;
  font-family: 'seoul-hangang-jung-l', 'Noto Serif KR', serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

html,
body,
#root,
#root > div {
  height: 100%;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
